import React, { Component } from 'react';
import { withRouter } from "react-router";
import { connect } from "react-redux";
import * as actions from "../../actions/ordersAction";
import { bindActionCreators } from 'redux';
import ProductCardComponent from "./ProductCardComponent";
import CommonHeaderComponent from "../headers/CommonHeaderComponent";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import * as symbols from "../../operations/symbols";
import LoaderComponent from '../core/LoaderComponent';
import Search from "sushiweb/Icons/all/Search";
import { symbol } from "prop-types";
import PictureModeModal from '../core/PictureModeModal';

class OrderProductsComponent extends Component {

  constructor(props) {
    super(props);

    this.state = {
      products: [],
      hasNoSearchResult: false,
      reasons: [],
      issues: {},
      totalItems: 0,
      issueItems: 0,
      isOpen: [],
      allReset: true,
      toggleDownWrapper: false, //toggle it with isOpen
      showInvoiceModal: false
    }
  }

  setIssue = (orderProductId, issue) => {
    var issues = this.state.issues;
    issues[[orderProductId]] = issue;
    this.setState({ issues, issueItems: Object.keys(issues).length })
  };


  deleteIssue = (orderProductId) => {
    var issues = this.state.issues;
    delete issues[[orderProductId]];
    this.setState({ issues, issueItems: Object.keys(issues).length })
  };

  setSearchVal(e) {
    const { products } = this.state;
    if (e.target.value != '') {
      const tempProducts = this.props.orders.orderProducts.filter((ele, index) => {
        if (ele.skuName.toLowerCase().includes(e.target.value.toLowerCase())) {
          return ele
        }
      });
      this.setState({ products: tempProducts, hasNoSearchResult: false })
    } else {
      this.setState({
        products: this.props.orders.orderProducts,
        hasNoSearchResult: true
      })
    }
  }

  componentDidMount() {
    this.props.actions.getAllIssueReasons('productIssue');
    this.props.actions.getOrderProducts(this.props.match.params.id)
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    const { getAllIssueReasonsStatus, orders, getOrderProductsStatus } = this.props;
    if (orders.reasons && getAllIssueReasonsStatus !== prevProps.getAllIssueReasonsStatus && getAllIssueReasonsStatus === symbols.STATUS_DONE) {
      var reasons = orders.reasons.map((ele) => ({ key: ele.id, value: ele.name, sendValue: ele.id, rules: ele.rules }));
      reasons.push({ key: 0, value: 'None', sendValue: 0, rules: null });
      this.setState({ reasons })
    }
    if (orders.orderProducts && getOrderProductsStatus !== prevProps.getOrderProductsStatus && getOrderProductsStatus === symbols.STATUS_DONE) {
      orders.orderProducts.forEach((ele) => {
        if (ele.issue !== null && ele.issue.quantityDelivered < ele.quantityDispatched) { this.setIssue(ele.id, ele.issue) }
      });
      this.setState({ products: orders.orderProducts, totalItems: orders.orderProducts.length })
      const isOpen = [];
      for (let i in orders.orderProducts) {
        isOpen[orders.orderProducts[i].id] = false;
      }

      this.setState({ isOpen })
    }
  }

  handleOnSubmit() {
    let invoiceImages = this.props.location.state.images;
    if(invoiceImages.length=== 0){
      this.setState({showInvoiceModal: true})
    } else {
      const id = this.props.match.params.id;
      this.props.history.push({
        pathname: '/order/' + id + "/invoice",
        state: { id, images: invoiceImages, orderNumber: this.props.location.state.orderNumber }
      })
    }

  }

  setIsOpen(id) {
    let isOpen = [...this.state.isOpen];
    if (isOpen[id] == true) {
      isOpen[id] = false;
      this.setState({ allReset: false })
    } else {
      for (let i in isOpen) {
        isOpen[i] = false;
      }
      isOpen[id] = true;
    }
    this.setState({ allReset: false })
    this.setState({ isOpen, toggleDownWrapper: true })
  }

  setClose() {
    let isOpen = [...this.state.isOpen];
    for (let i in isOpen) {
      isOpen[i] = false;
    }
    this.setState({ allReset: true })
    this.setState({ isOpen, toggleDownWrapper: false })
  }

  closePictureModeModal(){
    this.setState({showInvoiceModal: false})
  }



  render() {
    const id = this.props.match.params.id;
    const { hasNoSearchResult, showInvoiceModal } = this.state;
    return (
      <div className='pd-top-global gray-wrap mh-100'>
        <PictureModeModal
          isOpen={showInvoiceModal}
          closePictureModeModal={() => this.closePictureModeModal()}
          id={id}
          orderNumber={this.props.location.state.orderNumber}
          type={'invoice'}
          title = {'Upload Invoice'}
          images={this.props.location.state.images}
          redirectedURL={`/order/${id}/invoice`}
          modalBody={() => {
            return <div className="center mt-neg-2"> 
              <p className="fs-16 mb-2 align-left">
                Please take an image of the invoice and make sure that the amount and items are clearly visible
              </p>
              <img src="../../images/invoiceSample.png" />
             </div>
          }}
        />
        {
          !this.props.location.state ?
          <div>
            <CommonHeaderComponent title={`Products`} enableBack={true}/>
            <div className="pd-top-global fs-18 red-text center">
              Unauthorized!!!
            </div>
          </div>
          :
          this.props.getAllIssueReasonsStatus === symbols.STATUS_BEGIN || this.props.getOrderProductsStatus ===
          symbols.STATUS_BEGIN || this.props.saveProductIssuesStatus === symbols.STATUS_BEGIN ?
          <LoaderComponent/>
          :
          <div>
            <div className="order-top-header ">
              <div className="input-field-wrap">
                <input
                  type="text"
                  className="input-field__input search-input search-input--left-icon"
                  placeholder="Search Products"
                  onChange={(e) => this.setSearchVal(e)}
                />
                <div className="search-icon">
                  <Search className="icon-color-gray icon-fs-15"/> 
                </div>
              </div>
            </div>
            <CommonHeaderComponent title={`${this.props.location.state.orderNumber} - Products`} enableBack={true}/>
            <div className='pd-bottom-global'>
              <React.Fragment>
                {
                  this.state.products.map((ele) => <ProductCardComponent 
                    key={ele.id}
                    history={this.props.history}
                    product={ele}
                    reasons={this.state.reasons}
                    allReset={this.state.allReset}
                    setIssue={this.setIssue}
                    deleteIssue={this.deleteIssue}
                    setIsOpen={(id) => this.setIsOpen(id)}
                    setClose={() => this.setClose()}
                    isOpen={this.state.isOpen[ele.id]}/>)
                }
                {
                  this.state.products.length === 0 &&
                  <div className="warning fs-14 red-text">Products not found</div>
                }
              </React.Fragment>
            </div>
            <div className={this.state.toggleDownWrapper ? 
              "order-card fixed-verify-section z-highest toggleDown" :
              "order-card fixed-verify-section z-highest"}
            >
              <div className="">
                Total Items: <b>{this.state.totalItems}</b> <br/>
                Upd. Items: <b>{this.state.issueItems} </b>
              </div>
              <div
                className={"btn btn-green-flat btn-green "}
                onClick={() => this.handleOnSubmit()}
              >
                Complete Verification
              </div>
            </div>
          </div>
        }
      </div>
    )
  }
}

export default withRouter(connect(
  state => ({
    getAllIssueReasonsStatus: state.statuses.getAllIssueReasons.status,
    getAllIssueReasonsError: state.statuses.getAllIssueReasons.error,
    getOrderProductsStatus: state.statuses.getOrderProducts.status,
    getOrderProductError: state.statuses.getOrderProducts.error,
    orders: state.orders,
  }),
  dispatch => ({
    actions: bindActionCreators(actions, dispatch),
  }),
)(OrderProductsComponent));
