import React, { Component } from 'react';
import { connect } from 'react-redux';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { bindActionCreators } from 'redux';

import * as symbols from '../../operations/symbols';
import { getOrders, sendEvent } from '../../actions/ordersAction';
import CommonHeaderComponent from '../headers/CommonHeaderComponent';
import OrderCardComponent from './OrderCardComponent';
import StateFilterComponent from '../core/StateFilterComponent';
import PictureModeModal from '../core/PictureModeModal';
import ReasonListModal from '../core/ReasonListModal';
import LoaderComponent from '../core/LoaderComponent';
import Search from "sushiweb/Icons/all/Search";
import './orders.scss';

class OrdersComponent extends Component {

  constructor(props) {
    super(props);
    this.state = {
      openPictureModeModal: false,
      filterId: null,
      filterValue: '',
      searchVal: '',
      selectedId: null,
      openReasonListModal: false,
      selectedOrder: null
    }
  }

  componentDidMount() {
    const params = {
      daNumber: this.props.user.phone
    }
    this.props.actions.getOrders(params);
  }

  openPictureModeModal(id) {
    this.setState({
      openPictureModeModal: true,
      selectedId: id
    })
  }

  closePictureModeModal() {
    this.setState({ openPictureModeModal: false })
  }

  setSearchVal = (e) => {
    this.setState({ searchVal: e.target.value })
  }

  handleSearchFilter(e) {
    e.preventDefault();
    let params = {
      orderNumber: this.state.searchVal,
      daNumber: this.props.user.phone
    }

    let statusParams = {};

    if (this.state.filterId == 1) {
      statusParams.withImagesOnly = this.state.filterValue
    } else if (this.state.filterId == 4) { // for Undelivered orders
      params.status = 'pending'
    } else if (this.state.filterId == 5) { // for completed orders
      params.status = 'completed'
    } else if (this.state.filterId == 6) { // for cancelled orders
      params.withDeliveryIssues = 'true'
    }  else {
      statusParams.status = this.state.filterValue
    }

    params = { ...params, ...statusParams };
    this.props.actions.getOrders(params);

    return false
  }

  clearFilter(e) {
    e.stopPropagation();
    this.setState({ filterId: null, filterValue: '' }, () => {
      this.handleSearchFilter(e);
    });
  }

  handelFilterChange(e, filterId, value) {
    this.setState({ filterId, filterValue: value }, () => {
      this.handleSearchFilter(e)
    })
  }

  openReasonListModal(id, order) {
    const params = {
        entityId: id,
        eventName: 'cancellation_start'
      }
    this.props.actions.sendEvent(params);
    this.setState({ openReasonListModal: true, selectedId: id, selectedOrder: order })
  }

  closeReasonListModal(id) {
    this.setState({ openReasonListModal: false, selectedOrder: null })
  }

  render() {

    const { openPictureModeModal, closePictureModeModal, openReasonListModal, closeReasonListModal } = this.state;
    const { getOrdersStatus, orders } = this.props;

    return (
      <div className="hp-container gray-wrap pd-top-global">
        <PictureModeModal 
            isOpen={openPictureModeModal} 
            closePictureModeModal={() => this.closePictureModeModal()}
            id={this.state.selectedId}
        />
        <ReasonListModal
          isOpen={openReasonListModal} 
          closeReasonListModal={() => this.closeReasonListModal()}
          id={this.state.selectedId}
          selectedOrder={this.state.selectedOrder}
        />
        <CommonHeaderComponent
          title={orders && orders.length == 0 ? 'Orders' : 'Route'}
          routeId={orders && orders.length > 0 && orders[0].route}
        />
        <div className="order-top-header">
          <form className="no-style" onSubmit={(e) => this.handleSearchFilter(e)}>
            <div className="input-field-wrap">
              <input 
                type="text" 
                className="input-field__input search-input" 
                placeholder="Search order number"
                onChange={this.setSearchVal}
              />
              <button type="submit" className="search-icon">
                <Search className="icon-color-gray icon-fs-15"/>
              </button>
            </div>
          </form>
          <StateFilterComponent 
              filterId={this.state.filterId} 
              handelFilterChange={(e, id, value) => this.handelFilterChange(e, id, value)}
              clearFilter={(e) => this.clearFilter(e)}
          />
        </div>
        {
          getOrdersStatus == symbols.STATUS_BEGIN ?
            <LoaderComponent /> 
          :
           <React.Fragment>
       
          {
            this.props.orders && this.props.orders.length > 0 ?
            <React.Fragment>
              { this.props.orders.sort((a, b) => {
                // sort by keeping pending on top
                if(a.status!==b.status){
                  if(a.status==="pending"){
                    return -1
                  }else {
                    return 1
                  }
                }
                }).map((ele, index) => {
                    return(
                      <OrderCardComponent 
                        order={ele}
                        openReasonListModal={(id, ele) => this.openReasonListModal(id, ele)}
                        openPictureModeModal={(id) => this.openPictureModeModal(id)}
                        key={index}
                      />
                    )
                })
              }
              </React.Fragment> :
            <div className="black-text order-top-header mt-2">No Results Found</div>
          }
          </React.Fragment>
        }
      </div>
    )
  }
}

const actions = { getOrders, sendEvent}
export default connect(
  state => ({
    getOrdersStatus: state.statuses.getOrders.status,
    getOrdersError: state.statuses.getOrders.error,
    orders: state.orders.orders,
    user: state.auth.user
  }),
  dispatch => ({
    actions: bindActionCreators(actions, dispatch),
  }),
)(OrdersComponent);
