import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { bindActionCreators } from 'redux';

import * as symbols from '../../operations/symbols';
import { ORDER_STATUS } from '../../operations/constants';
import * as actions from '../../actions/ordersAction'
import { getBase64, getLocation } from '../../operations/operations';
import CommonHeaderComponent from '../headers/CommonHeaderComponent';
import PictureModeModal from '../core/PictureModeModal';
import { PhotoViewer, PhotoMainViewer } from '../core/PhotoViewerComponent';
import LoaderComponent from '../core/LoaderComponent';
import Input from "sushiweb/InputTypes/Input";
import Call from "sushiweb/Icons/all/Call";

class OrderDetailComponent extends Component {

  constructor(props) {
    super(props);

    this.state = {
      openPictureModeModal: false,
      position: {
        longitude: 0,
        latitude: 0
      },
      phones: [],
      noPhonesData: false,
      imagesUploaded: [],
      showCommentAddBtn: false,
      remark: ''
    }
  }

  componentDidMount() {
    this.props.actions.getOrderDetails(this.props.match.params.id);
    this.props.actions.getOrderPhones(this.props.match.params.id);
  }

  addRemark(e) {
    e.preventDefault();
    const payload = {
      orderId: this.props.match.params.id,
      remark: this.state.remark,
      remarkBy: 'Da',
    }
    this.props.actions.addRemarks(payload)
  }

  cancelRemark() {
    this.setState({ remark: '' })
  }

  getStatusBtn(status) {
    return (
      <div className="order-value">
        {
          status === ORDER_STATUS.pending &&
          <div className="btn btn-yellow btn-sm btn-flat nohover">
            Pending
          </div>
        }
        {
          status === ORDER_STATUS.rejected &&
          <div className="btn btn-red btn-sm btn-flat nohover">
            Rejected
          </div>
        }
        {
          status === ORDER_STATUS.approved &&
          <div className="btn btn-green btn-sm btn-flat nohover">
            Approved
          </div>
        }
      </div>
    )
  }

  getVerifiedBtn(status) {
    return (
      <React.Fragment>
        {
          status === ORDER_STATUS.delivered ?
            <div className="btn btn-info btn-sm nohover">
              OTP Verified
            </div>
          :
          status ===  ORDER_STATUS.cancelled?
          <div className="btn btn-red-smoke btn-sm nohover">
            Cancelled
          </div>
          :
          <div className="btn btn-yellow btn-sm nohover">
            Pending
          </div>
        }
      </React.Fragment>
    )
  }

  closePictureModeModal() {
    this.setState({ openPictureModeModal: false })
  }

  sendToOrder() {
    this.handleSelectedImage();
  }

  componentDidUpdate(prevProps) {
    const { uploadImagesStatus, remarkStatus, getPhonesStatus, orders, orderDetailImagesStatus } = this.props;
    if (uploadImagesStatus !== prevProps.uploadImagesStatus && uploadImagesStatus === symbols.STATUS_DONE) {
      this.props.actions.removeImageFromOrder(this.props.match.params.id);
      this.props.actions.getOrderDetails(this.props.match.params.id);
    }
    if (orders.orderPhones && getPhonesStatus !== prevProps.getPhonesStatus && getPhonesStatus === symbols.STATUS_DONE) {
      this.setState({ phones: orders.orderPhones })
    }
    if (getPhonesStatus !== prevProps.getPhonesStatus && getPhonesStatus === symbols.STATUS_FAIL) {
      this.setState({ noPhonesData: true })
    }

    if (uploadImagesStatus !== prevProps.uploadImagesStatus && uploadImagesStatus === symbols.STATUS_FAIL) {
      document.getElementById('takePicture').value = '';
      this.props.actions.resetOrderDetailImages();
    }

    if (remarkStatus !== prevProps.remarkStatus && remarkStatus === symbols.STATUS_DONE) {
      // const orderId = this.props.match.params.id;
      // this.props.actions.setRemarkTemp(orderId);
      document.getElementById('add-comment').blur();
      this.cancelRemark()
    }
    if (orderDetailImagesStatus !== prevProps.orderDetailImagesStatus && orderDetailImagesStatus === symbols.STATUS_DONE) {
      this.sendToOrder();
      this.props.actions.resetOrderDetailImagesStatus();
    }
  }


  handleSelectedImage(e) {
    let payload = new FormData();
    payload.append('orderId', this.props.match.params.id);
    if (this.props.orders.uploadedImages[this.props.match.params.id].length == 1) {
      payload.append('files', this.props.orders.uploadedImages[this.props.match.params.id][0]);
    } else {
      for (let i = 0; i < this.props.orders.uploadedImages[this.props.match.params.id].length; i++) {
        payload.append('files', this.props.orders.uploadedImages[this.props.match.params.id][i]);
      }
    }

    payload.append('latitude', this.state.position.latitude);
    payload.append('longitude', this.state.position.longitude);
    payload.append('imageType', "order_image");
    this.props.actions.uploadImages(payload)
  }

  handleDeliverClick(paymentMode, orderNumber) {

    const id = this.props.match.params.id;
    const params = {
      entityId: parseInt(id),
      eventName: 'verify_products_start'
    }
    this.props.actions.sendEvent(params);
    let invoiceImages = this.props.orders.orderDetails.images.reduce((acc, ele) => {
      if (ele.entityType === 'invoice') {
        acc.push(ele)
      }
      return acc
    }, [])
    this.props.history.push({
      pathname: '/order/' + id + '/products',
      state: { paymentMode, orderNumber, images: invoiceImages }
    })

  }

  // Image handlers
  handleSelectedImageChange(e, type) {
    let files = e.target.files;
    this.setState({ imagesUploaded: files }, () => {
      this.props.actions.setOrderDetailImages(this.props.match.params.id, files, type);

      document.getElementById('takePicture').value = ''
    })
  }

  handleImageInputClick(type) {
    document.getElementById(type).click((e) => {})
  }


  openPictureModeModal() {
    this.handleImageInputClick('takePicture');
    // this.setState({ openPictureModeModal: true })
  }

  toggleCommentAddBtn(e) {
    e.preventDefault();
    this.setState(prevState => ({ showCommentAddBtn: !this.state.showCommentAddBtn }))
  }

  handleCommentValue(e) {
    this.setState({ remark: e.target.value })
  }

  render() {
    const { orders, getOrdersStatus, uploadImagesStatus, uploadImagesError } = this.props;
    const { openPictureModeModal, closePictureModeModal, showCommentAddBtn } = this.state;
    const id = this.props.match.params.id;
    if (getOrdersStatus == symbols.STATUS_BEGIN || !orders.orderDetails) {
      return (
        <LoaderComponent/>
      )
    }
    return (
      <div className="hp-container pd-top-global mh-100 white-wrap pd-bottom-global">
        {/*<PictureModeModal
          isOpen={openPictureModeModal}
          closePictureModeModal={() => this.closePictureModeModal()}
          hasSameRoute={true}
          id={id}
        />*/}
        <input hidden type="file" accept="image/*" capture="camera" id="takePicture" onChange={(e, type) => this.handleSelectedImageChange(e, this.props.type)}/>
        <CommonHeaderComponent title={`Order - ${orders.orderDetails.orderDetail.orderNumber}`} enableBack={true}/>
        <div className="order-card mt-neg-2">
          <div className="orders-body mt-1">
            <div className="row">
              <div className="col-xs-12">
                <div className=" subtitle">
                  Status &nbsp; <span>{this.getVerifiedBtn(orders.orderDetails.orderDetail.status)}</span>
                </div>
              </div>
            </div>
            <div className="row mt-2">
              <div className="col-xs-6">
                <div className=" subtitle">
                  Outlet
                </div>
                <div className=" value">
                  {
                    
                    orders.orderDetails.orderDetail.fetchedStatus ? orders.orderDetails.orderDetail.outletName:"fetching data"
                  }
                </div>
              </div>
              <div className="col-xs-6">
                <div className="subtitle">
                  Phone #
                </div>
                <div className=" value">
                  <FontAwesomeIcon icon="phone" className="blue-text"/> 
                  <a href={`tel:${(this.state.phones.length)>0?this.state.phones[0].phoneNumber:""}`}> +91 {(this.state.phones.length)>0?this.state.phones[0].phoneNumber:""}</a>
                </div>
              </div>
            </div>

            <div className="row mt-2">
              <div className="col-xs-6">
                <div className="subtitle">
                  Address
                </div>
                <div className="value">
                  {orders.orderDetails.orderDetail.outletAddress ? orders.orderDetails.orderDetail.outletAddress : '-'}
                </div>
              </div>
              <div className="col-xs-6">
                <div className="subtitle">
                  Delivery Slot
                </div>
                <div className=" value">
                  {orders.orderDetails.orderDetail.slotTime ? orders.orderDetails.orderDetail.slotTime : '-'}
                </div>
              </div>
            </div>

            
            {/*CTA for verify delivery*/}
            
            {
              showCommentAddBtn ?
                <div className="fixed-verify-section flex-right z-highest order-card"> 

                  <div className="align-right">
                    {
                      this.props.remarkStatus == symbols.STATUS_BEGIN ? 
                      <div className="btn btn-green btn-sm">
                        <FontAwesomeIcon icon="spinner" className="animate-spinner"/>
                      </div> :
                      <div className={this.state.remark.trim() === '' ? "btn btn-disabled btn-sm" : "btn btn-green btn-sm"} onMouseDown={(e) => this.addRemark(e)}>Add Comment</div>
                    }
                  </div>
                </div> : 
                <React.Fragment>
                {
                  orders.orderDetails.orderDetail.otpVerified ?
                  <React.Fragment>
                
                  <div className="fixed-verify-section z-highest order-card">
                    <div className="btn btn-expand btn-disabled">
                      Already Delivered
                    </div>
                  </div>
                  </React.Fragment>
                  :
                  <React.Fragment>
                    <div className="fixed-verify-section z-highest order-card"> 
                    <div className="btn btn-expand btn-green"
                      onClick={() => this.handleDeliverClick(orders.orderDetails.orderDetail.paymentMode, orders.orderDetails.orderDetail.orderNumber)}
                    >
                      Verify products
                    </div>
                  </div>
                </React.Fragment>
              }
              </React.Fragment>
            }
            <div className="row">
              <div className="col-xs-12">
                <div className="order-photos mt-2">
                  <div className="subtitle">
                    Photos
                  </div>
                  <PhotoViewer
                    parentClass="order-photo-wrapper"
                    className="order-photo-ele "
                    photos={orders.orderDetails.images}
                    id={id}
                    removeCross={true}
                    name="imageUrl"
                    uploadImagesStatus={uploadImagesStatus}
                    uploadImagesError={uploadImagesError}
                    showRemoveImage={true}
                    showPhotoViewer={true}
                    removeImage={(e, index, imaegs, id) => this.props.actions.removeImage(e, index, orders.uploadedImages[id], id)}
                    openPictureModeModal={() => this.openPictureModeModal()}
                  />
                </div>
              </div>
            </div>
            <div className="order-photos mt-2">
              <div className="subtitle">
                Comments
              </div>
              {
                orders.orderDetails.remarks.length > 0 && orders.orderDetails.remarks.map((ele, index) => {
                  return (
                    <React.Fragment key={index}>
                      <div className="mt-1" >
                        {ele.message}
                      </div>
                      <div className="mb-1 gray-text">
                        by {ele.userType}
                      </div>
                    </React.Fragment>
                  )
                })
              }
              <form className="mt-2 relative fs-14">
                <textarea
                  onChange={(e) => this.handleCommentValue(e)}
                  value={this.state.remark}
                  id="add-comment" 
                  onFocus={(e) => this.toggleCommentAddBtn(e)}
                  onBlur={(e) => this.toggleCommentAddBtn(e)}
                >
                </textarea>
                <label className={this.state.showCommentAddBtn || this.state.remark && this.state.remark !== '' ? "animate-label up" : "animate-label"}>
                  <span className={this.state.showCommentAddBtn ? 'green-text' : ''}>Add comment</span>
                </label>
              </form>
            </div>

            <div className="light-line mt-2"></div>
                <div className="">
                  In case of any other issues feel free to &nbsp;<Call className="sushi-icon icon-fs-16 blue-text vertical-align-bottom"/>
                  <a href='tel:+911171565093' className="blue-link"> Call Us</a>
                </div>
          </div>
        </div>
      </div>
    )
  }
}

export default withRouter(connect(
  state => ({
    getOrdersStatus: state.statuses.getOrderDetails.status,
    getOrdersError: state.statuses.getOrderDetails.error,
    remarkStatus: state.statuses.addRemarks.status,
    uploadImagesStatus: state.statuses.uploadImages.status,
    uploadImagesError: state.statuses.uploadImages.error,
    getPhonesStatus: state.statuses.getOrderPhones.status,
    getPhonesError: state.statuses.getOrderPhones.error,
    orderDetailImagesStatus: state.statuses.orderDetailImages.status,
    orders: state.orders,
    // status: state.statuses.login.status,
  }),
  dispatch => ({
    actions: bindActionCreators(actions, dispatch),
  }),
)(OrderDetailComponent));
