import { path } from 'ramda';
import actionTypes from './actionTypes';
import api from '../operations/api/api';
import { ApiAddresses, DefaultParams } from '../common/apiAddresses';
import { makeQueryParams } from "../operations/operations";
import moment from 'moment';

export const setOrderDetailImages = function(id, images, imageType) {
  return dispatch => {
    if (imageType === 'reason') {
      dispatch({ type: actionTypes.ORDER_RESAON_DETAIL_IMAGES_DONE, images, id });
    } else if (imageType === 'invoice') {
      dispatch({ type: actionTypes.INVOICE_IMAGES_DONE, images, id });
    } else {
      dispatch({ type: actionTypes.ORDER_DETAIL_IMAGES_DONE, images, id });
    }
  };
};

export const resetOrderDetailImagesStatus = function() {
  return dispatch => {
    dispatch({ type: actionTypes.ORDER_RESAON_DETAIL_IMAGES_BEGIN });
    dispatch({ type: actionTypes.INVOICE_IMAGES_BEGIN });
    dispatch({ type: actionTypes.ORDER_DETAIL_IMAGES_BEGIN });
  };
};

export const resetOrderDetailImages = function() {
  return dispatch => {

    dispatch({ type: actionTypes.RESET_ORDER_DETAIL_IMAGES_DONE });
  };
};

export const removeImageFromOrder = function(id) {
  return dispatch => {
    dispatch({ type: actionTypes.REMOVE_DETAIL_IMAGES_ORDER_DONE, id });
  };
};

export const removeImage = function(e, index, images, id) {
  return dispatch => {
    let imagesModified = images.filter((ele, currIndex) => {
      if (currIndex != index) {
        return ele
      }
    })
    dispatch({ type: actionTypes.REMOVE_DETAIL_IMAGES_DONE, images: imagesModified, id });
  };
};

export const logoutAction = function(payload) {
  return dispatch => {
    window.localStorage.removeItem('token');
    dispatch({ type: actionTypes.LOGOUT_DONE });
  };
};

export const getOrderDetails = function(id) {
  return dispatch => {
    dispatch({ type: actionTypes.GET_ORDER_DETAILS_BEGIN });

    api()
      .get(`${ApiAddresses.getOrderDetails}${id}`)
      .then(({ data, headers }) => {
        dispatch({ type: actionTypes.GET_ORDER_DETAILS_DONE, orderDetails: data.response });
      })
      .catch(err => {
        dispatch({
          type: actionTypes.GET_ORDER_DETAILS_FAIL,
          errors: path(['response', 'data'], err),
        });
      })
  };
};

