export const getOrders = function(params) {
  return dispatch => {
    let currentDateTime = moment().format('DD-MM-YYYY');
    const queryParams = DefaultParams.getOrders;
    queryParams.targetDeliveryDate = currentDateTime;
    let queryString = makeQueryParams(queryParams, params);
    dispatch({ type: actionTypes.GET_ORDERS_BEGIN });

    api()
      .get(`${ApiAddresses.getOrders}${queryString}`)
      .then(({ data, headers }) => {
        dispatch({ type: actionTypes.GET_ORDERS_DONE, orders: data.response.orders });
      })
      .catch(err => {
        dispatch({
          type: actionTypes.GET_ORDERS_FAIL,
          errors: path(['response', 'data'], err),
        });
      })

  };
};

