export const saveProductIssues = function(payload, productId, orderId, issueId) {
  return dispatch => {
    dispatch({ type: actionTypes.SAVE_PRODUCT_ISSUES_BEGIN });
    dispatch({ type: actionTypes.DELETE_PRODUCT_ISSUES_BEGIN });
    let apiMock;
    if (issueId) {
      apiMock = (urlConfig, payload) => {
        return api()
          .put(urlConfig + '/' + issueId, payload)
      }
    } else {
      apiMock = (urlConfig, payload) => {
        return api()
          .post(urlConfig, payload)
      }
    }
    apiMock(`${ApiAddresses.saveProductIssues}${orderId}/products/${productId}/issues`, payload)
      .then(({ data, headers }) => {
        if (issueId) {
          dispatch({ type: actionTypes.SAVE_PRODUCT_ISSUES_DONE, updatedProductIssue: issueId, productId });
        } else {
          dispatch({ type: actionTypes.SAVE_PRODUCT_ISSUES_DONE, updatedProductIssue: data.response, productId });
        }
      })
      .catch(err => {
        dispatch({
          type: actionTypes.SAVE_PRODUCT_ISSUES_FAIL,
          errors: path(['response', 'data'], err),
        });
      })
  };
};

export const deleteProductIssues = function(productId, orderId, issueId) {
  return dispatch => {
    dispatch({ type: actionTypes.DELETE_PRODUCT_ISSUES_BEGIN });
    const apiMock = (urlConfig, payload) => {
      return api()
        .delete(urlConfig + '/' + issueId)
    }
    apiMock(`${ApiAddresses.saveProductIssues}${orderId}/products/${productId}/issues`)
      .then(({ data, headers }) => {

        dispatch({ type: actionTypes.DELETE_PRODUCT_ISSUES_DONE, updatedProductIssue: null, productId });

      })
      .catch(err => {
        dispatch({
          type: actionTypes.DELETE_PRODUCT_ISSUES_FAIL,
          errors: path(['response', 'data'], err),
        });
      })

  };
};

