import React, { Component } from 'react';
import { connect } from 'react-redux';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { bindActionCreators } from 'redux';
import { withRouter } from 'react-router';

import * as symbols from '../../operations/symbols';
import * as actions from "../../actions/ordersAction";
import './orders.scss';

class OrderCardComponent extends Component {

  constructor(props) {
    super(props);

    this.state = {}
  }

  getStatusBtn(status, hasImages = false) {
    return (
      <div className="order-value">
        {
          status == 'pending' && hasImages == false &&
          <div className="btn btn-yellow btn-sm nohover">
            Pending
          </div>
        }

        {
          status == 'pending' && hasImages == true &&
          <div className="btn btn-disabled btn-sm nohover">
            Approval Pending
          </div>
        }

        {
          status == 'rejected' && 
          <div className="btn btn-red btn-sm nohover">
            Rejected
          </div>
        }
        {
          status == 'approved' && 
          <div className="btn btn-green btn-sm nohover">
            Approved
          </div>
        }
      </div>
    )
  }

  getVerifiedBtn(status) {
    return (
      <div className="order-value">
            {
                status === "delivered" ?
                    <div className="btn btn-info btn-sm nohover">
                      OTP Verified
                    </div>
                :
                  status === "cancelled"?
                    <div className="btn btn-red-smoke btn-sm nohover">
                      Cancelled
                    </div>
                    :
                    <div className="btn btn-yellow btn-sm nohover">
                      Pending
                    </div>
                  
            }
            </div>
    )
  }

  getPhoneNumber(phones) {
    if (phones.length > 0) {
      return (<React.Fragment><FontAwesomeIcon icon="phone" className="blue-text"/> +91 {phones[0].phoneNumber}</React.Fragment>)
    } else {
      return "-"
    }
  }

  sendToDetail(id, type) {
    if (type == 'start') {
      const params = {
        entityId: id,
        eventName: 'delivery_start'
      }
      this.props.actions.sendEvent(params);
    }
    this.props.history.push('/order/' + id)
  }

  render() {
    const { order } = this.props;
    return (
      <div className="order-card mt-1 mb-1">
        <div className="order-title">
          <div className="row">
                <div className="col-xs-6">
                    <div className="subtitle fs-15">
                        Order - <span className="okra-med">{order.orderNumber}</span>
                    </div>
                </div>
                <div className="col-xs-6 align-right">
                    <div className="value">
                        {
                          this.getVerifiedBtn(order.status)
                        }
                    </div>
                </div>

            </div>
        </div>
        <div className="light-line"></div>
        <div className="orders-body">
            <div className="row">
              <div className="col-xs-6">
                <div className="mt-1 subtitle">
                    Outlet
                </div>
                <div className=" value">
                    {order.outletName}
                </div>
              </div>
              <div className="col-xs-6">
                    <div className="mt-1 subtitle">
                      Phone
                    </div>
                    <div className=" value">
                      <a href={`tel:${this.getPhoneNumber(order.phones)}`}>
                        {this.getPhoneNumber(order.phones)}
                      </a>
                    </div>
              </div>
            </div>
             <div className="row">
              <div className="col-xs-12">
                <div className="mt-1 subtitle">
                    Address
                </div>
                <div className=" value">
                    {order.outletAddress}
                </div>
              </div>
              
            </div>
            <div className="row">
              <div className="col-xs-6">
                    <div className="mt-1 subtitle">
                      Delivery Slot
                    </div>
                    <div className=" value">
                      <div className="mt-1 value">
                      {order.slotTime === '' ? '-' : order.slotTime}
                  </div>
                    </div>
              </div>
            </div>
            {
              !order.fetchedStatus ?
                <div className="btn btn-expand btn-transparent mt-2">
                  Updating order details...
                </div>
              :
              
              <React.Fragment>
                {
                  !(order.status === "cancelled") ?
                    <React.Fragment>
                      {
                        order.status !== "delivered" ?
                          <React.Fragment>
                            <div className="btn btn-expand btn-green mt-2"
                                 onClick={(id) => this.sendToDetail(order.orderId, 'start')}>
                              Start Delivery Process
                            </div>
                            {
                              <div className="btn btn-expand btn-transparent mt-1"
                                   onClick={(id) => this.props.openReasonListModal(order.orderId)}>
                                Cancel Delivery
                              </div>
                            }
                          </React.Fragment>
                          :
                          <div className="btn btn-expand btn-transparent-green btn-normal mt-2"
                               onClick={(id) => this.sendToDetail(order.orderId, 'delivery')}>
                            View details
                          </div>
                      }
                    </React.Fragment>
                    : null
                }
              </React.Fragment>
            }
        </div>
      </div>
    )
  }
}

export default withRouter(connect(
  state => ({
    // errors: state.statuses.login.errors,
    // status: state.statuses.login.status,
    user: state.auth.user
  }),
  dispatch => ({
    actions: bindActionCreators(actions, dispatch),
  }),
)(OrderCardComponent));
