import React, { Component } from 'react';
import Searchbox from "sushiweb/Searchbox";
import Dropdown from "sushiweb/Dropdown";
import MenuItem from "sushiweb/MenuItem";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

class DropdownComponent extends Component {

  constructor(props) {
    super(props);
    this.state = {
      showMenu: false
    }
  }

  handleSelect(ele) {
    this.setState({
      selectedFilter: {
        key: ele.key,
        value: ele.value,
        sendValue: ele.sendValue
      },
      showMenu: false
    }, () => {
      this.props.handleClick(this.state.selectedFilter)
    });
  }

  listenEvent = (e) => {
    let isEnableClick = (e.target.matches('.dropdown-wrapper') || e.target.matches('.dropdown-custom') || e.target.matches('.dropdown-head'));
    if (!isEnableClick) {
      this.setState({ showMenu: false });
    }
  }

  componentDidMount() {
    window.addEventListener('click', this.listenEvent)
  }

  componentWillUnmount() {

    window.removeEventListener('click', this.listenEvent)
  }

  render() {
    const { menu, defaultParam, primary } = this.props;
    const { showMenu } = this.state;
    return (
      <React.Fragment>
        <div className={primary ? `dropdown-wrapper dropdown-wrapper-primary ${this.props.className}` :  `dropdown-wrapper dropdown-filter-wrapper ${this.props.className}`} onClick={() => this.setState({showMenu: !this.state.showMenu})}>
          <div className="dropdown-custom">
            <div className="dropdown-head">{defaultParam.value}</div>
            {
              showMenu && 
              <div className="dropdown-content">
                {menu.map((ele, index) => {
                    return(
                      <div 
                        className={ele.value == defaultParam.value ? "dropdown-ele active" : "dropdown-ele"} 
                        onMouseDown={() => this.handleSelect(ele)}
                        key={index}
                      >{ele.value}</div>
                    )
                  })
                }
              </div>
            }
          </div>
          <FontAwesomeIcon icon="chevron-down" className="icon"/>
        </div>
      </React.Fragment>
    )
  }
}

export default DropdownComponent;
