import React, { Component } from 'react';
import { withRouter } from "react-router";
import { connect } from "react-redux";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { bindActionCreators } from 'redux';

import * as actions from "../../actions/ordersAction";
import * as symbols from "../../operations/symbols";
import { PhotoMainViewer } from '../core/PhotoViewerComponent';
import CommonHeaderComponent from "../headers/CommonHeaderComponent";
import PictureModeModal from '../core/PictureModeModal';
import LoaderComponent from '../core/LoaderComponent';


class InvoiceComponent extends Component {

  constructor(props) {
    super(props);

    this.state = {
      photos: [],
      images: []
    }
  }

  openPictureModeModal() {
    this.setState({ openPictureModeModal: true })
  }

  closePictureModeModal() {
    this.setState({ openPictureModeModal: false })
  }

  setImages(files) {
    this.closePictureModeModal();
    const photos = [...this.state.photos];
    for (let i = 0; i < files.length; i++) {
      photos.push(files[i])
    }
    this.setState({ photos })
  }

  componentDidMount() {
    this.props.actions.getOrderDetails(this.props.match.params.id);
    if (this.props.location.state && this.props.orders && this.props.orders.invoiceImages[this.props.location.state.id]) {
      const photos = [...this.props.orders.invoiceImages[this.props.location.state.id]]
      this.setState({ photos })
    }
  }

  componentDidUpdate(prevProps) {
    const { uploadImagesStatus, getOrdersStatus } = this.props;
    if (uploadImagesStatus !== prevProps.uploadImagesStatus && uploadImagesStatus === symbols.STATUS_DONE) {
      const params = {
        entityId: parseInt(this.props.match.params.id),
        eventName: 'verify_products_complete'
      }
      this.props.actions.sendEvent(params);
      this.props.history.push({
        pathname: `/order/${this.props.match.params.id}/verifyDelivery`,
        state: { id: this.props.match.params.id, orderNumber: this.props.location.state.orderNumber }
      })
    }

    if (getOrdersStatus !== prevProps.getOrdersStatus && getOrdersStatus === symbols.STATUS_DONE) {
      let invoiceImages = this.props.orders.orderDetails.images.reduce((acc, ele) => {
        if (ele.entityType === 'invoice') {
          acc.push(ele)
        }
        return acc
      }, [])
      this.setState({ images: invoiceImages })
    }
  }

  removeImage(e, currIndex) {
    let photos = [...this.state.photos];
    photos = photos.filter((ele, index) => {
      if (currIndex !== index) {
        return true
      }
    })
    this.setState({ photos })
  }

  uploadInvocies() {
    let payload = new FormData();
    payload.append('orderId', this.props.location.state.id);
    if (this.state.photos.length == 1) {
      payload.append('files', this.state.photos[0]);
    } else {
      for (let i = 0; i < this.state.photos.length; i++) {
        payload.append('files', this.state.photos[i]);
      }
    }

    payload.append('latitude', 0);
    payload.append('longitude', 0);
    payload.append('imageType', "invoice");
    this.props.actions.uploadImages(payload)
  }

  pushToVerify() {
    if (this.state.photos.length > 0) {
      let payload = new FormData();
      payload.append('orderId', this.props.location.state.id);
      if (this.state.photos.length == 1) {
        payload.append('files', this.state.photos[0]);
      } else {
        for (let i = 0; i < this.state.photos.length; i++) {
          payload.append('files', this.state.photos[i]);
        }
      }
      payload.append('latitude', 0);
      payload.append('longitude', 0);
      payload.append('imageType', "invoice");
      this.props.actions.uploadImages(payload)
    } else {
      const params = {
        entityId: parseInt(this.props.match.params.id),
        eventName: 'verify_products_complete'
      }
      this.props.actions.sendEvent(params);
      this.props.history.push({
        pathname: `/order/${this.props.match.params.id}/verifyDelivery`,
        state: { id: this.props.match.params.id, orderNumber: this.props.location.state.orderNumber }
      })
    }
  }

  render() {
    const {
      openPictureModeModal,
      closePictureModeModal
    } = this.state;

    const { getOrdersStatus } = this.props;

    if (!this.props.location.state) {
      return (
        <React.Fragment>
          <CommonHeaderComponent title={`Invoice`} enableBack={true}/>
          <div className="pd-top-global fs-18 red-text center">
            Unauthorized!!!
          </div>
        </React.Fragment>
      )
    }
    return (
      <div className="pd-top-global gray-wrap mh-100">
        {/*Open modal for picture click*/}
        <PictureModeModal
          isOpen={openPictureModeModal}
          closePictureModeModal={() => this.closePictureModeModal()}
          hasSameRoute={true}
          default={true}
          hasNotWrapper={true}
          setImages={(files) => this.setImages(files)}
        />
        <CommonHeaderComponent title={`Invoice - ${this.props.location.state.id}`} enableBack={true}/>
        <div className="order-top-header fs-14">
            <div className="subtitle">
              <b>Uploaded Invoices</b>
            </div>
            {
              getOrdersStatus == symbols.STATUS_BEGIN ? 
              <LoaderComponent /> : null
            }
            {
              this.state.images
              && this.state.images.length > 0 &&
              <div className="order-photos ">
                <PhotoMainViewer
                  parentClass="order-photo-wrapper"
                  className="order-photo-ele "
                  photos={this.state.images}
                  id={this.props.location.state.id}
                />
              </div>
            }
          <div className="order-photos mt-2">
            {/*<div className="subtitle">*/}
            {/*  <b>Add Photos</b>*/}
            {/*</div>*/}
            <div className="horizontal-scroll">
            
            <div className="order-photo-wrapper">
              <div 
                className="gallery-item order-photo-ele order-photo-placeholder"
                onClick={() => this.openPictureModeModal()}
              >
                <FontAwesomeIcon icon="camera" />
              </div>
            </div>
            {
              this.state.photos && this.state.photos.map((ele, index) => {
                return(
                  <div className="order-photo-wrapper">
                      <div className="cross-image" onClick={(e) => this.removeImage(e, index)}>
                          <FontAwesomeIcon icon="times" />
                      </div>
                      <div className={`gallery-item order-photo-ele`} style={{'backgroundImage': 'url('+URL.createObjectURL(ele)+')'}}></div>
                  </div>
                )     
              })
            }
            </div>
            <div className="fixed-verify-section z-highest order-card">
            {
              this.state.images.length == 0 ? <React.Fragment>
              {
                this.props.uploadImagesStatus == symbols.STATUS_BEGIN ?
                <div className="btn btn-green btn-expand"><FontAwesomeIcon icon="spinner" className="animate-spinner" /></div> :
                <div className={this.state.photos && this.state.photos.length > 0 ? "btn btn-green btn-expand " : "btn btn-expand  btn-disabled"} onClick={() => this.uploadInvocies()}>Upload invoice</div>
                
              }
              </React.Fragment> :
              <React.Fragment>
              {
                this.props.uploadImagesStatus == symbols.STATUS_BEGIN ?
                <div className="btn btn-green btn-expand"><FontAwesomeIcon icon="spinner" className="animate-spinner" /></div> :
                <div className="btn btn-green btn-expand " onClick={() => this.pushToVerify()}>Upload invoice</div>
              }
              </React.Fragment>
              
            }
            </div>
            {
              this.props.uploadImagesStatus == symbols.STATUS_FAIL && this.props.uploadImagesError && <div className="red-text mt-2">{this.props.uploadImagesError.error.message}</div>
            }

            
          </div>
        </div>
      </div>
    )
  }
}

export default withRouter(connect(
  state => ({
    getOrdersStatus: state.statuses.getOrderDetails.status,
    getOrdersError: state.statuses.getOrderDetails.error,
    uploadImagesStatus: state.statuses.uploadImages.status,
    uploadImagesError: state.statuses.uploadImages.errors,
    orders: state.orders,
  }),
  dispatch => ({
    actions: bindActionCreators(actions, dispatch),
  }),
)(InvoiceComponent));
